import { useState, useEffect } from 'react'
import SingleCard from './components/SingleCard'
import LostModal from './components/LostModal'
import WinModal from './components/WinModal'
import confetti from 'canvas-confetti'
import './App.css'

import snow1 from './img/lou-in-snow.png'
import snow2 from './img/jo-in-snow.png'
import beach from './img/beach-babies.png'
import goggles from './img/jojo-goggles.png'
import sunglasses from './img/lou-sunglasses.png'
import rainBoots from './img/rain-boots.png'




const cardImages = [
  { src: snow1, matched: false },
  { src: snow2, matched: false },
  { src: beach, matched: false },
  { src: goggles, matched: false },
  { src: sunglasses, matched: false },
  { src: rainBoots, matched: false }
]


function App() {
const [ cards, setCards ] = useState([])
const [ turns, setTurns ] = useState(12)
const [ choiceOne, setChoiceOne ] = useState(null)
const [ choiceTwo, setChoiceTwo ] = useState(null)
const [ disabled, setDisabled ] = useState(false)
const [ lostModal, setLostModal ] = useState(false)
const [ winModal, setWinModal ] = useState(false)
const [ showButton, setShowButton ] = useState(true)


  //shuffle cards for new game
  const shuffleCards = () => {
    const shuffledCards = [...cardImages, ...cardImages]
      .sort(()=> Math.random() - 0.5)
      .map((card) => ({ ...card, id: Math.random() }))

    setShowButton(true)
    setCards(shuffledCards)
    setTurns(12)
    setLostModal(false)
    setWinModal(false)
  }

  console.log(cards)
  //handle picking cards
const handleChoice =(card)=>{
  choiceOne ? setChoiceTwo(card) : setChoiceOne(card)
}


//compare 2 selected cards
useEffect(() => {

  if(choiceOne && choiceTwo) {
    setDisabled(true)
    if(choiceOne.src === choiceTwo.src) {
      setCards(prevCards => {
        return prevCards.map(card => {
          if(card.src === choiceOne.src) {
            return { ...card, matched: true }
          } else {
            return card
          }
        })
      })
      resetTurn();
      } else {
      setTimeout(()=>resetTurn(),1000)
  }
}
}, [choiceOne, choiceTwo])

const winner = cards.filter(card => card.matched === true).length


useEffect(() => {
  if(turns === 0 && winner > 0) {
    setLostModal(true)
    setCards([])
    setShowButton(false)
  } else if(winner === 12) {
      setWinModal(true)
      // setCards([])
      setShowButton(false)
      setTimeout(()=>{confetti({
        particleCount: 200,
        spread: 180,
      })}, 1000)
      setTimeout(()=>setWinModal(false),3500)
      setTimeout(()=>setShowButton(true),3500)
    } else {
    }
}, [turns, winner])


const resetTurn = () => {
  setChoiceOne(null)
  setChoiceTwo(null)
  setTurns(prevTurns => prevTurns - 1)
  setDisabled(false)
  }

  return (
    <div className="App">
      <h1>Memory Match</h1>
          <p>You have twelve turns to make a match.</p>

      {showButton && (
        <button onClick={shuffleCards}>New Game</button>
      )}

      {lostModal && (
      <LostModal shuffleCards={shuffleCards} />
      )}

      {winModal && (
        <WinModal shuffleCards={shuffleCards} className="card-grid" /> 
      )}

      {!winModal && (
        <div className="card-grid">
        {cards.map(card=> (
          <SingleCard 
          key={card.id}
          card={card}
          handleChoice={handleChoice} 
          flipped={card === choiceOne || card === choiceTwo || card.matched}
          disabled={disabled}
          /> 
        ))}
      </div>)}
      <p>Turns Left: {turns}</p>
    </div>
  );
}


export default App